import React, { Component } from 'react';
import Slider from "react-slick";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';

class EventList extends Component {

  _isMounted = false;

  static defaultProps = {
    data: []
  };
  state = { responsive: true };

  componentDidMount() {
    this._isMounted = true;
    this.updateWindowDimensions();
    window.addEventListener("resize", this.updateWindowDimensions);
  }

  componentWillUnmount() {
    this._isMounted = false;
    window.removeEventListener("resize", this.updateWindowDimensions);
  }

  updateWindowDimensions = () => {
    const { responsive } = this.state;

    if (this._isMounted) {
      if (responsive && window.innerWidth > 640) {
        this.setState({responsive:false})
      } else if (!responsive && window.innerWidth <= 640) {
        this.setState({responsive:true})
      }
    }
  };

  clickBanner(index) {
    const { data } = this.props;
    window.open(data[index].link, '_blank');
  }

  render() {
    // console.log("EventList render()");
    const { data } = this.props;
    const { responsive } = this.state;

    let result, settings;

    var showEventBanner = false;
    if (this._isMounted) {
      if ((responsive && process.env.REACT_APP_EVENT_BANNER_MOBILE === "true") || (!responsive && process.env.REACT_APP_EVENT_BANNER_PC === "true"))
      showEventBanner = true;
    }

    if (showEventBanner) {
      if (!responsive) {
        settings = {
          dots: false,
          infinite: true,
          speed: 500,
          slidesToShow: 1,
          slidesToScroll: 1,
          centerMode: true,
          // fade: true,
          adaptiveHeight: true,
          variableWidth: true,
          autoplay: true,
          autoplaySpeed: 3000,
          pauseOnHover: true,
          focusOnSelect: false,
          centerPadding: '100px',
          className: 'scSlide'
        };

        result = (
          <Slider {...settings}>
            {data.map(
              (item, index) => {
                return (
                  <a href={item.link} key={index} className="anchor" target="_blank" rel="noopener noreferrer">
                    <div className="carousel">
                      <img className="image" src={item.image} alt={item.link}/>
                      <div className="sticker_evt">
                        <div className="mall" mall={item.mallName}/>
                      </div>
                    </div>
                  </a>
                )
              }
            )}
          </Slider>
        );
      } else {
        // result = (<div/>);
        settings = {
          autoPlay: true,
          infiniteLoop: true,
          showThumbs: false,
          dynamicHeight: true,
          swipeable: false
        };

        result = (
          <Carousel {...settings} onClickItem={(index) => { this.clickBanner(index); }}>
            {data.map(
              (item, index) => {
                return (
                  <div className="carousel" key={index}>
                    <img key={index} className="image" src={item.image} alt={item.mallName}/>
                    <div className="sticker_evt">
                      <div className="mall" mall={item.mallName}/>
                    </div>
                  </div>
                )
              }
            )}
          </Carousel>
        );
      }
    } else {
      result = (<div/>);
    }
    return result;
  }
}

export default EventList;
