import React from 'react';

class ProductInfo extends React.Component {
  shouldComponentUpdate(nextProps, nextState) {
    // 수정 상태가 아니고, info 값이 같다면 리렌더링 안함
    if (nextProps.item === this.props.item) {
      return false;
    }
    // 나머지 경우엔 리렌더링함
    return true;
  }

  handleShare = (e) => {
    alert('share');
  };

  handleClickItem = (item, e) => {
    window.location.href = item.link;
  };

  render(){
    const { item } = this.props;

    return (
      <li className="sc_item">
        <a href={item.link} className="sc_anchor" target="_blank" rel="noopener noreferrer">
          <div className="sc_image">
            <div className="sc_thumbnail"><img alt={item.title} src={item.image}/></div>
            <div className="sc_sticker">
              <div className="sc_mall" mall={item.mallName}/>
            </div>
          </div>
          <div className="sc_desc">
            <p className="sc_title">{item.title}</p>
            <div className="sc_price_area">
              <strong className="sc_price_desc">{item.discount}</strong>
              <span className="sc_original_price">
                <i className="sc_num">{item.orgPrice}</i>
              </span>
              <span className="sc_price">
                <i className="sc_num">{item.price}</i>
              </span>
            </div>
            {/*<Popup trigger={<button className="btn" id="btnShare" onClick={this.handleShare} aria-label="공유"/>}*/}
                   {/*position="right top" contentStyle={{ width: "195px" }} className="icon_list_popup">*/}
            {/*</Popup>*/}
          </div>
        </a>
      </li>
    );
  }
}

export default ProductInfo;
