import Consts from '../consts'
import React, { Component } from 'react';
import Popup from "reactjs-popup";

class Header extends Component {
  _isLogin = false;

  constructor(props) {
    super(props);

    window.addEventListener('beforeinstallprompt', (event) => {
      // Stash the event so it can be triggered later.
      window.deferredPrompt = event;
      // Remove the 'hidden' class from the install button container
      this.refs['btnInstall'].removeAttribute('hidden');
      // divInstall.classList.toggle('hidden', false);
    });

    window.addEventListener('appinstalled', (event) => {
      console.log(process.env.PUBLIC_URL + ' App was installed.', event);
    });

    this._isLogin = this.props.cookies.get('sc_t');
  }

  componentDidMount() {
  }

  //https://developers.google.com/web/fundamentals/codelabs/your-first-pwapp/?hl=ko
  handleInstall = (e) => {
    const promptEvent = window.deferredPrompt;
    if (!promptEvent) {
      // The deferred prompt isn't available.
      return;
    }
    // Show the install prompt.
    promptEvent.prompt();
    // Log the result
    promptEvent.userChoice.then((result) => {
      // Reset the deferred prompt variable, since
      // prompt() can only be called once.
      window.deferredPrompt = null;
      // Hide the install button.
      this.refs['btnInstall'].setAttribute('hidden', true);
    });

    // e.preventDefault();
    // this.props.onCreate();
  };

  handleRefresh = (e) => {
    this.props.onRefresh(true);
    window.scroll(0, 0);
  };

  handleLogout = (e) => {
    this.props.cookies.remove('sc_t', { path: '/' , domain: process.env.REACT_APP_COOKIE_DOMAIN});

    this._isLogin = false;
  };

  handleMenu = (e) => {
    alert('menu');
  };

  render() {
    const linkList = Consts.SET_OF_LINKS.map(
      (item, index) => (
        <div key={index} className="icon_item">
          <a href={item.link} className="sc_anchor">
            <img className="icon" src={item.image} alt={item.name}/>
            <span className="desc">{item.name}</span>
          </a>
        </div>
    ));

    return (
      <header className="sc_header">
        <Popup trigger={<button id="btnMenu" className="headerButton" aria-label="메뉴"/>} position="right top" contentStyle={{ width: "270px" }} className="icon_list_popup">
          <div className="icon_list">
            {linkList}
          </div>
        </Popup>
        <div className="sc_logo">
          <a href={process.env.PUBLIC_URL + '/'}>
            <img src={process.env.PUBLIC_URL + '/logo.png'} alt="로고" />
          </a>
        </div>
        {!this._isLogin &&
        <Popup trigger={<button id="btnLogin" className="headerButton" aria-label="로그인"/>}
               position="left top" onClose={this.handleLogout} contentStyle={{ width: "195px" }} className="icon_list_popup">
          <div className="menu">
            <div className="menuItem">
              <a
                href={'https://nid.naver.com/oauth2.0/authorize?response_type=code&client_id=' + process.env.REACT_APP_NAVER_CLIENTID + '&redirect_uri=' + Consts.NAVER_LOGIN_CALLBACK_URL}>
                <img className="icon_only" src={process.env.PUBLIC_URL + '/assets/images/naverlogin.png'} alt="네이버 아이디로 로그인"/>
              </a>
            </div>
          </div>
        </Popup>
        }
        {this._isLogin &&
        <Popup trigger={<button id="btnProfile" className="headerButton" aria-label="내정보"/>}
               position="left top" contentStyle={{ width: "130px" }} className="icon_list_popup">
          <img className="icon_only" onClick={this.handleLogout} src={process.env.PUBLIC_URL + '/assets/images/naverlogout.png'} alt="네이버 아이디 로그아웃"/>
        </Popup>
        }
        <button id="btnInstall" className="headerButton" ref="btnInstall" onClick={this.handleInstall} aria-label="설치" hidden/>
        <button id="btnRefresh" className="headerButton" onClick={this.handleRefresh} aria-label="새로고침"/>
      </header>
    );
  }
}

export default Header;