import React, { Component } from 'react';
import './sc.css';

import axios from 'axios';
import Header from "./components/Header";
import ProductList from "./components/ProductList";
import EventList from "./components/EventList";
import ScrollButton from "./components/ScrollButton";
import MenuButton from "./components/MenuButton";

var pageSize=20;

class App extends Component {
  _isLoading = false;

  state = {
    productList: [],
    events: null,
    activeIndex: 0,
    menus: null,
    loading: false
  };

  componentDidMount() {
    var menuSplit = String(process.env.REACT_APP_MENUS).split(';');
    var menus = [];
    var idx = 0;

    const { menu } = this.props.match.params;

    menuSplit.forEach(function (item, index) {
      var menuItem = String(item).split(':');
      var menuIndex = menus.push({displayName:menuItem[0],queryName:menuItem[1]}) - 1;
      if (menuItem[1] === menu) {
        idx = menuIndex;
      }
    });

    if (process.env.REACT_APP_EVENT_BANNER_PC === "true" || process.env.REACT_APP_EVENT_BANNER_MOBILE === "true") {
      this.getEvents();
    }
    window.addEventListener("scroll", this.handleScroll);

    this.setState({menus:menus}, () => this.getProducts(true, idx));
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll = () => {
    const { innerHeight } = window;
    const { scrollHeight } = document.body;
    // IE에서는 document.documentElement 를 사용.
    const scrollTop =
      (document.documentElement && document.documentElement.scrollTop) ||
      document.body.scrollTop;

    if (scrollHeight - innerHeight - scrollTop < 100) {
      this.getProducts(false);
    }
  };

  getEvents = () => {
    axios.get('https://api.smartconsumer.kr/api/getEvents?&size=' + pageSize)
      .then(res => {
        const event_list = res.data;
        this.setState({ events: event_list});
      });
  };

  getProducts = (init, idx) => {
    if (!this._isLoading) {
      this._isLoading = true;
      const { productList, activeIndex, menus } = this.state;
      var index = activeIndex;
      if (idx !== 'undefined' && idx >= 0) {
        index = idx;
      }
      var productType = menus[index].queryName;
      var lastPage = 1;

      this.setState({loading:true, activeIndex: index});

      // 최초 요청
      if (init || !productList[index]) {
        console.log("type:" + productType + " page:" + lastPage);
        axios.get('https://api.smartconsumer.kr/api/getProducts?page=' + lastPage + '&size=' + pageSize  + '&name=' + productType)
          .then(res => {
            const product_list = res.data;
            productList[index] = {products: product_list, lastPage: lastPage, noMoreProducts: false};
            this.setState({ productList: productList});
          });
      } else {
        if (!productList[index].noMoreProducts) {
          lastPage = productList[index].lastPage + 1;
          console.log("type:" + productType + " page:" + lastPage);
          axios.get('https://api.smartconsumer.kr/api/getProducts?page=' + lastPage + '&size=' + pageSize + '&name=' + productType)
            .then(res => {
              const product_list = res.data;

              if (product_list.products.length > 0) {
                product_list.products.map(item =>
                  productList[index].products.products.push(item)
                );
                productList[index].lastPage = lastPage;
              } else {
                productList[index].noMoreProducts = true;
              }
            });

          this.setState({productList: productList});
        }
      }

      this.setState({loading:false});
      this._isLoading = false;
    }
  };

  handleMenuClick = (index) => {
    this.getProducts(false, index);
  };

  render() {
    const { productList, activeIndex, events, loading, menus } = this.state;

    var products = null;

    if (productList[activeIndex] && productList[activeIndex].products) {
      products = productList[activeIndex].products;
    }
    var list = null;
    if (menus != null) {
      list = menus.map(
        (item, index) => (
          <MenuButton key={item.queryName} index={index} isActive={this.state.activeIndex === index} onClick={this.handleMenuClick}
                      name={item.displayName}/>
        )
      );
    }

    return (
      <div>
        <Header onRefresh={this.getProducts} cookies={this.props.cookies}/>
        <main>
          <div className="home">
            {
              events &&
              <EventList
                data={events.products}
              />
            }
            <div align="center">
              { list }
            </div>
            {
              products !== null &&
              <ProductList
                data={products.products}
              />
            }
            <h1 align="center" hidden={!loading}>상품정보를 불러오고 있습니다...</h1>
          </div>
          <ScrollButton scrollStepInPx="200" delayInMs="0"/>
        </main>
      </div>
    );
  }
}

export default App;
