import React from 'react';

class MenuButton extends React.Component {
  handleClick = () => this.props.onClick(this.props.index, this.props.query);

  render() {
    return <button
      type='button'
      className={
        this.props.isActive ? "sc_menu_button selected" : "sc_menu_button"
      }
      onClick={ this.handleClick }
    >
      <span>{ this.props.name }</span>
    </button>
  }
}

export default MenuButton;
